.header {
  background : rgb(0, 0, 0);
  display: table ;
  width : 100% ;
}
.item {
  text-align : center ;
  padding-top : 5px ;
  padding-bottom : 5px;
  display : table-cell ;
  color : white ;
  text-decoration : none ;
  font-size : 1.1rem ;
}
.item:hover {
  background : orange ;
}
.item:active {
  background : white;
  color : rgb(255, 255, 255);
}
