.line {
    text-decoration: none;
    font-weight: bolder;
    color: rgb(249, 233, 170);
    -webkit-text-stroke-width: 1.2px;
    -webkit-text-stroke-color: black;
    font-size: 20px;
    display: inline;
}

.line:hover {
    color: orange;
    -webkit-text-stroke-width: 1.2px;
    -webkit-text-stroke-color: black;
    font-size: 25px;
}

